import Vue from 'vue'
import Router from 'vue-router'
import Welcome from '@/page/welcome/Welcome'
import Create from '@/page/create/Create'
import Login from '@/page/login/Login'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Welcome',
      component: Welcome
    },
    {
      path: '/create',
      name: 'Create',
      component: Create
    },
    {
      path:'/login',
      name: 'Login',
      component: Login
    }
  ]
})



