<template>
    <div id="create_body">
        <h1>注册用户</h1>
        <span>
          <label>姓名：</label>
          <input type="text"/><br/>
        </span>
        <span>
          <label>密码：</label>
          <input type="password"/><br/>
        </span>
        <span>
          <label>确认密码：</label>
          <input type="password"/><br/>
        </span>
        <span>
          <label>手机号码：</label>
          <input type="text"/><br/>
        </span>
        <span>
          <label>家庭住址：</label>
          <input type="text"/><br/>
        </span>
        <span>
          <label>用户身份：</label>
          <select>
            <option value="病人">病人</option>
            <option value="医生">医生</option>
            <option value="护士">护士</option>
          </select><br/>
        </span>
        <span>
          <input id="create_submit" type="submit" title="注册" />
        </span>
    </div>
</template>

<script>
    export default {
        name: "CreateBody"
    }
</script>

<style lang="stylus" scoped>
  #create_body
    z-index 100px
    position absolute
    background white
    opacity 0.70
    top 150px
    left 500px
    height 550px
    width 500px
    border-radius 50px
    text-align center
    font-size 20px
    input
      height 28px
      position relative
      margin-top 20px
      padding 5px
      border-radius 5px
    select
      height 28px
      position relative
      margin-top 20px
      padding 5px
      border-radius 5px
    #create_submit
      height 40px
      width 100px
      border-radius 10px

</style>



