<template>
  <div id="login_body">
    <h1>登陆用户</h1>
    <span>
          <label>用户身份：</label>
          <select v-model="statue">
            <option value="病人">病人</option>
            <option value="医生">医生</option>
            <option value="护士">护士</option>
            <option value="管理员">管理员</option>
          </select><br/>
        </span>
    <span>
    <span>
          <label>姓名：</label>
          <input type="text"/><br/>
        </span>
    <span>
          <label>密码：</label>
          <input type="password"/><br/>
        </span>
    <span>
          <label>确认密码：</label>
          <input type="password"/><br/>
        </span>
    <span v-if="isShow">
          <label>手机号码：</label>
          <input type="text"/><br/>
        </span>
    <span v-if="isShow">
          <label>家庭住址：</label>
          <input type="text"/><br/>
        </span>
          <input id="login_submit" type="submit" title="登陆" />
        </span>
  </div>
</template>

<script>
    export default {
      name: "LoginBody",
      data() {
        return {
          statue: '',
          isShow: true
        }
      },
      watch:{
        statue: 'changeStatue'
      },
      methods:{
        changeStatue(statue){
          if (statue == '管理员') {
            this.isShow = false
          }else {
            this.isShow = true
          }
        }
      }
    }
</script>

<style lang="stylus" scoped>
  #login_body
    z-index 100px
    position absolute
    background white
    opacity 0.40
    top 150px
    left 500px
    height 550px
    width 500px
    border-radius 50px
    text-align center
    font-size 20px
    input
      height 28px
      position relative
      margin-top 20px
      padding 5px
      border-radius 5px
    select
      height 28px
      position relative
      margin-top 20px
      padding 5px
      border-radius 5px
    #login_submit
      height 40px
      width 100px
      border-radius 10px
</style>



